import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'


import Vue from 'vue'
// 导入网络请求的包
import { $http } from '@escook/request-miniprogram'

// 在 uni-app 项目中，可以把 $http 挂载到 uni 顶级对象之上，方便全局调用
uni.$http = $http
// 这是请求更路径
$http.baseUrl = 'https://api-hmugo-web.itheima.net'

// 配置请求拦截器
$http.beforeRequest = function (options) {
	uni.showLoading({
		title:'数据加载中'
	})
}

// 配置响应拦截器
// 请求完成之后做一些事情
$http.afterRequest = function () {
  uni.hideLoading()
}

// 封装弹窗的方法
uni.$showMsg = function (title = '数据请求失败',duration = 1500) {
	uni.showToast({
		title,
		duration,
		icon:'none'
	})
}
Vue.config.productionTip = false
App.mpType = 'app'
const app = new Vue({
    ...App
})
app.$mount()